body{
  background-color: rgb(48, 75, 108);
}

.App {
  text-align: center;
  color: white;
  letter-spacing: .2px;

}

.Republican{
  background-color: rgb(196, 0, 0);
}

.Democratic{
  background-color: rgb(24, 20, 83);
}
.Unknown-nonpartisan{
  background-color: rgb(67, 67, 77);
}
.Green-party{
  background-color: rgb(13, 116, 17);
}

.candidate{
  border-radius: 10px;
  margin: 20px;
  padding: 10px;
  width: 206px;
  max-width: 206px;
  display: inline-block;
  vertical-align: top;
  min-height: 330px;
  height: auto;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
.candidate:hover{
  -webkit-box-shadow: 0px 1px 1px 5px white;
          box-shadow: 0px 1px 1px 5px white;
}
.candidate h3{

text-align: center;
margin-bottom: 5px;
}

h2{
  
  border-bottom: 2px solid white;
  width: 70%;
  text-align: center;
  margin: 20px auto;
}

button{ 
  margin: 5px;
  background: white;
  width: 5em;
  height: 2em;
  cursor: pointer;
}


.image-container{
  width: 180px;
  height: 180px;
  margin: 10px auto;
  border-radius: 5px;
  background-size: cover;
  background-position: center;
}

.image-container img{
  width: 100%;
  
}

.social {
  border-top: 2px solid white;
}

.social a, .website{
  display: inline-block;
  padding: 10px;
  color: white;
  border: 2px solid white;
  font-size: 1em;
  width: 1em;
  height: 1em;
  border-radius: 100px;
  margin: .5em;
  vertical-align: middle;
  line-height: 1em;
}

.social a:hover,.social a:focus, .website:focus, .website:hover{
  color: rgb(255, 196, 86);
  
}

.info{
  max-width: 100%;
  text-align: left;
  padding: 0px 10px;
  margin: 10px 0px;
}

#register, #polls{
  color: white;
  font-weight: bold;
  text-decoration: none;
  background-color: rgba(217, 111, 6, 1);
  padding: 20px 30px;
  display: inline-block;
  margin: 20px;
  margin-bottom:20px;
  border-radius: 5px;
}

#register:hover, #register:focus, #polls:hover, #polls:focus{
  background-color:  rgb(196, 104, 13);
}

.readmore, a{
  color: white;
  font-weight: bold;
}
.top-p{
  max-width: 600px;
  margin: 30px auto;
  padding: 0px 20px;
  line-height: 1.6em;
}

.hidden 
{position:absolute;
left:-10000px;
top:auto;
width:1px;
height:1px;
overflow:hidden;}

#footer{
  border-top: 2px solid white;
  font-size: .8em;
  padding: 20px;
}


